@import '../sass/mixins/_mixins.sass';

.sel {
  display: flex;
  justify-content: space-between;
  border-radius: 4px;
  background-color: $text-d-2;
  padding: 1.2rem;
  &__res {
    border-radius: 4px;
    background-color: $text-d-2;
    width: 100%;
    display: flex;
    padding: .4rem;
    justify-content: space-between;
    @include lg {
      flex-direction: column;
    }
    &-col {
      width: calc(50% - .2rem);
      padding: 1rem 1.2rem;
      @include lg {
        width: 100%;
      }
      span {
        @include font(1.6rem, 1.5em, 600);
        display: block;
      }
      div {
        @include font(1.4rem, 1.43em, 500);
      }
      &--1 {
        border-radius: 2px;
        background-color: rgba(40, 42, 46, 0.8);
        span {
          font-family: 'Gilroy';
          color: $white;
        }
      }
      &--2 {
        color: $white;
        span {
          font-family: 'Druk Wide Cy Web Super';
        }
      }
    }
  }

  input {
    @include font(1.6rem, 1.75em, 600);
    font-family: Gilroy;
    line-height: 1.75;
    text-align: center;
    color: $white;
    background: transparent;
    height: 2.8rem;
    outline: none !important;
    border: none !important;
    font-family: 'Gilroy';
  }

  &-item {
    cursor: pointer;
    display: flex;
    svg {
      g {
        @include transition;
      }
    }
    &:hover {
      svg {
        g {
          opacity: 1;
        }
      }
    }
  }

  &-desc {
    margin: .8rem 0 2.4rem;
    padding-left: 1.2rem;
    @include font(1.3rem, 1.23em, 500);
    color: $text-d-60;
  }
}

.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: opacity 0.3s ease;
  @include lg {
    align-items: flex-end;
  }
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
  @include lg {
    vertical-align: bottom;
    width: 100%;
  }
}

.modal-container {
  position: relative;
  z-index: 100;
  width: 872px;
  border-radius: 12px;
  -webkit-backdrop-filter: blur(40px);
  backdrop-filter: blur(40px);
  box-shadow: 32px 32px 96px 0 rgba(9, 13, 20, 0.4), -16px -16px 32px 0 $text-d-text-d-4, 0 16px 16px 0 rgba(9, 13, 20, 0.4);
  border: solid 1px $bg-l-6;
  background-image: linear-gradient(117deg, $bg-l-12, $bg-l-6 52%, rgba(245, 247, 250, 0) 100%);
  display: flex;
  @include lg {
    width: 100%;
    border-radius: 1.2rem 1.2rem 0 0;
  }
}

.modal {
  &-overlay {
    background-color: rgba(9, 13, 20, 0.8);
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }

  &-submit {
    padding-top: 2.4rem;
    margin-top: auto;
    width: 100%;
    @include lg {
      padding-top: 3.2rem;
    }
    // .btn-color,
    // .l-primary-button,
    // .l-primary-button__button,
    // .btn span {
    //   width: 100% !important;}
  }

  &-close-button {
    position: absolute;
    top: 4.8rem;
    right: 4.8rem;
    z-index: 10;
    cursor: pointer;
    display: flex;
    background: transparent;
    @include lg {
      top: 3rem;
      right: 3rem;
    }
    path {
      @include transition;
    }
    &:hover {
      path {
        fill-opacity: .48;
      }
    }
  }

  &-body {
    padding: 4.8rem;
    position: relative;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    @include lg {
      padding: 3.2rem;
    }
  }

  &-img {
    padding: .4rem;
    border-radius: .8rem;
    @include lg {
      display: none;
    }
  }

  &-header {
    @include font(3rem, 1.2em, 400);
    // font-family: DrukWideCyTT-Super;
    color: #fff;
    margin: 0 0 .8rem;
    font-family: 'Druk Wide Cy Web Super';
    @include lg {
      font-size: 2.4rem;
      line-height: 1.33em;
      margin: 0 0 .4rem;
    }
    span {
      color: $secondary;
    }
  }

  &-desc {
    @include font(1.6rem, 1.5em, 600);
    color: $white;
    margin-bottom: 2.4rem;
  }
}

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
